@import '../../styles/mixin';
@import '../../styles/reset';
@import '../../styles/vars';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: sticky;
  top: 0;

  height: 48px;
  padding-left: 16px;

  background-color: $colorWhite;
  box-shadow: 0px 1px 0px $colorElements;

  @include onDesktop {
    padding-left: 24px;
    height: 64px;
  }

  &__logo {
    width: 64px;
    height: 21px;

    @include onDesktop {
      width: 80px;
      height: 28px;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
  }

  &__icons {
    display: none;
    align-items: center;

    @include onTablet {
      display: flex;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;

    @include onDesktop {
      width: 64px;
      height: 64px;
    }
  }

  &__cart {
    background-image: url(../../icons/cart.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__favourites {
    background-image: url(../../icons/favourites.svg);
    background-repeat: no-repeat;
    background-position: center;

    box-shadow: 0 0 0 1px $colorElements;
  }

  &__burger {
    display: block;
    width: 48px;
    height: 48px;
    border: 1px solid $colorElements;

    background-image: url(../../icons/union.svg);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;

    @include onTablet {
      display: none;
    }
  }
}
